import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
//  MUI   Bootstrap 

import './lib/mui/css/mui.css'
//  MUI 

// 
import './lib/mui/css/icons-extra.css'
Vue.config.productionTip = false

Vue.directive("focus",{
  inserted:function(el){
    el.focus();
  }
})

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
